import Trip from '../../components/common/trip.vue'
import Buslist from '../../components/common/buslist.vue'
export default {	
	components:{
		Trip,
		Buslist
	},
	data() {
		return {
			title: 'Hello',
			form:{
			},
			isLogin:true,
			widths:[10,0],
			width1:0,
			flag:false,
			list:['光谷大道五里湾','光谷大道金融港','光谷大三李陈','光谷大道关南村','光谷大道当代国际花园','光谷大道现代世贸中心'],
			childrenList:[],
			imgUrl:"",
			childCheck:null,//当前选中的是哪个学生
		}
	},
	watch:{
		widths:function (oldval,newval){
			console.log(oldval)
		},
		deep:true
	},
	onLoad() {
		this.imgUrl=this.$imgurl
		uni.authorize({
			scope:'userInfo'
		})
	},
	onShow(){
		let id=uni.getStorageSync("childId")
		if(id){
			this.childCheck=id
		}
		this.getuserinfo()
	},
	methods: {
		getuserinfo(){
			this.$http.post("puchildren/listByParentId",{
				parentId:1
			}).then(res=>{
				if(res.code==100){
					this.childrenList=res.info
					if(!this.childCheck){
						this.childCheck=res.info[0].id
						uni.setStorageSync("childId",res.info[0].id)
					}
				}
			})
		},
		changeChild(item){
			// 切换孩子
			// console.log(item)
			this.childCheck=item.id
			uni.setStorageSync("childId",item.id)
		},
		goRouter(){
			if(this.isLogin){
				//查看个人资料
				// this.goInfo(2)
				uni.navigateTo({
					url:"../../components/msg/userinfo"
				})
			}else{
				//登陆
				uni.navigateTo({
					url:"../my/login"
				})
			}
			
		},
		lookDetail(){
			console.log("点击")
			this.flag=false
			// this.widths[0]=10
			//运行当中===>线路详情
			uni.navigateTo({
				url:"../../components/msg/linedetails"
			})
		},
		touchStart(e,index){
			console.log("拖动小车")
			this.flag=true
			// console.log(e)
			let page=e.touches[0].pageX
			// console.log(page)
			// console.log(index)
			switch(index){
				case 0:
				// this.widths[index]=page
				this.width1=page
				// console.log(this.widths[0])
			}
			
		},
		touchEnd(){
			if(this.flag){
				console.log("查看线路安全报告")
				uni.navigateTo({
					url:"../../components/msg/security"
				})
			}
			
		},
		goInfo(index){
			switch(index){
				case 1:
					// 行程记录
					uni.navigateTo({
						url:"../../components/msg/Itineraryrecord"
					})
					break
				case 2:
					// this.goRouter()
					//投诉上传
					uni.navigateTo({
						url:"../../components/complaint/index"
					})
					break
				case 3:
					uni.navigateTo({
						url:"../../components/info/trip"
					})
					break
			}
			
		}
	}
}
